
.page {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
    overflow: auto;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 0px 20px 0px 20px;
}

.page h3 {
    font-size: 20px;
    color: #eee;
}

.page p {
    color: #ddd;
}

.outer-element {
    margin-left: 20px;
    margin-right: 20px;
}

h1.outer-element {
    margin-top: 20px;
    margin-bottom: 10px;
}

h3.outer-element {
    margin-top: 15px;
    margin-bottom: 5px;
}

.row, p.outer-element {
    margin-top: 5px;
    margin-bottom: 5px;
}

.outer-container {
    padding: 10px 20px 10px 20px;
    box-sizing: border-box;
}

.page input {
    background-color: #3b3b42;
}

.page-block {
    margin-top: 10px;
    background-color: #383a42;
    border-top: 1px solid #505359;
    border-bottom: 1px solid #505359;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 20px 0 20px;
    box-sizing: border-box;
}

.page-block input, .page-block select {
    background-color: #2d2e34;
}

.page-list {
    border-radius: 20px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    border: 1px solid #4c4f54;
}

.page-list-item {
    background-color: #35363d;
    border-bottom: 1px solid #4c4f54;
    padding: 7px 7px 7px 15px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
}

.page-list-item p {
    margin: 0;
    font-size: 14px;
}

.selected-dataset {
    border-top: 1px solid #4d4d5d;
}

.selected-dataset:last-child {
    border-bottom: 1px solid #4d4d5d;
}
